//vuevuex
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store = new Vuex.Store({//
    state: {
       tabbarIndex:uni.getStorageSync('tabbarIndex')?uni.getStorageSync('tabbarIndex'):0, //
	   tabItem:[{
			"pagePath": "pages/index/index",
			"text": ""
		}, {
			"pagePath": "pages/report/dailyReport/getStatList",
			"text": ""
		}],
		imgSever:'http://120.37.137.59:9900/upload/' ,  //
    },
    mutations: {
        updateTabbarIndex(state, index) {
           state.tabbarIndex=index;
        },
    },
	actions:{
		updateTabbarIndex({commit},index){
			commit('updateTabbarIndex',index)
		}
	}
})
export default store